import sdk, { ScryptedDeviceBase, ScryptedDeviceType, ScryptedInterface, Setting, Settings, SettingValue, WritableDeviceState } from "@scrypted/sdk";
import { StorageSetting, StorageSettings, StorageSettingsDict } from "@scrypted/sdk/storage-settings";
import MqttClient from './mqtt-client';
import { BaseMixin } from "./baseMixin";
import axios from "axios";
import https from 'https';

const { systemManager } = sdk;

interface HaEntityState {
    entity_id: string,
    state: string,
    last_reported: string,
    attributes: {
        device_class: string,
        friendly_name: string,
        unit_of_measurement: string,
        last_seen: string
    }
}

const httpsAgent = new https.Agent({
    rejectUnauthorized: false,
});

export type BaseSettingsKey = 'pluginEnabled'
    | 'mqttEnabled'
    | 'debug'
    | 'devNotifier'
    | 'useMqttPluginCredentials'
    | 'mqttHost'
    | 'mqttUsename'
    | 'mqttPassword'
    | 'haEnabled'
    | 'useHaPluginCredentials'
    | 'haAccessToken'
    | 'haAddress'
    | 'haProtocol'

